<script>
import DataList from "@/components/datalist";
export default {
    name: "khzb-list",
    components: { DataList },
    data() {
        return {
            filter: {
                contractSignDate: "",
                demandName: "",
                orderNo: "",
                orderStatus: "",
                supplyAgentName: ""
            },
            orderStatusList: [
                { value: 0, text: "" },
                { value: 1, text: "" },
                { value: 2, text: "" },
                { value: 3, text: "" },
                { value: 4, text: "" },
                { value: 5, text: "" },
                { value: 6, text: "" },
                { value: 7, text: "" },
                { value: 8, text: "" },
                { value: 9, text: "" },
                { value: 10, text: "" },
                { value: 11, text: "" },
                { value: 12, text: "" },
                { value: 13, text: "" },
                { value: 14, text: "" }
            ],
            processList: [],
            accessoryList: [],
            info: {},
            fileDetailList: [],
            layer: false,
            statusList: {
                0: "",
                1: "",
                3: "",
                4: ""
            }
        };
    },
    mounted() {},
    methods: {
        flush() {
            this.filter = {
                contractSignDate: "",
                demandName: "",
                orderNo: "",
                orderStatus: "",
                supplyAgentName: ""
            };
            this.$refs.list.update(true);
        },
        detail(item) {
            this.layer = true;
            this.getInfo(item.orderNo);
            this.getAccessory(item.orderNo);
            this.getProcess(item);
            this.$http
                .get(`/haolifa/order-product/accessory?orderNo=${item.orderNo}`)
                .then(res => {
                    this.fileDetailList = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getInfo(orderNo) {
            this.$http
                .get(`/haolifa/order-product/details?orderNo=${orderNo}`)
                .then(res => {
                    this.info = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getAccessory(orderNo) {
            this.$http
                .get(
                    `/haolifa/flowInstance/flow/accessoryInfo?formNo=${orderNo}&formId=0`
                )
                .then(res => {
                    res.forEach(item => {
                        if (item.fileUrl != "") {
                            this.accessoryList.push(item);
                        }
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getProcess(item) {
            this.$http
                .get(
                    `/haolifa/flowInstance/flow/progress/?formNo=${
                        item.orderNo
                    }`
                )
                .then(res => {
                    this.processList = [];
                    res.map(item => {
                        this.processList.push(item);
                        if (item.child.length > 0) {
                            item.child.map(it => {
                                this.processList.push(it);
                            });
                        }
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.accessoryList = [];
        },
        getOrderStatus(value) {
            let str = "";
            this.orderStatusList.map(item => {
                if (value == item.value) str = item.text;
            });
            return str;
        }
    }
};
</script>

